import React, { Component } from 'react'
import Card from '../Shared/CardUI'
import img1 from '../assets/img1.jpg'
import img2 from '../assets/img2.jpg'
import img3 from '../assets/img3.jpg'

class Cards extends Component {
  render () {
    return (
      <div className='container-fluid d-flex justify-content-center'>
        <div className='row'>
          <div className='col-md-4'>
            <Card
              imageSource={img1}
              altText='Business Applications'
              cardTitle='Business Applications'
              cardText="ERP,Oracle EBS, SAP, ERP for SME's (cloud and on premises)
    "
            />
          </div>
          <div className='col-md-4'>
            <Card
              imageSource={img2}
              altText='Solutions'
              cardTitle='Solutions'
              cardText='HCM (HR/PAYROLL),  eCommerce Solutions, Websites, RPA
              '
            />
          </div>
          <div className='col-md-4'>
            <Card
              imageSource={img3}
              altText='Services'
              cardTitle='Services'
              cardText='Infra Management, Server Upgrades, Disaster recovery and High availability, Cloud transformation, Virtualisation, Digital Transformation,Database Services: Oracle ad SQL server installations, Upgrades and tuning., HA ad Disaster recovery using Oracle Golden Gate, Data Gaurd, RMAN, RAC, Huma Resource Augmentation
              '
            />
          </div>
        </div>
      </div>
    )
  }
}

export default Cards
